@import "../../styles/include-media";

.social-links {
  max-width: 150px;
  width: 100%;
  position: absolute;
  top: calc(50% + 135px);
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-around;
  a {
    display: block;
    background: #ffffff;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border: 1px solid #ffffff;
    border-radius: 50%;
    color: #222222;
    font-size: 14px;
    cursor: pointer;
    transition: all .3s ease;
    &:hover {
      @include media('>980px') {
        transform: scale(1.2);
        color: #ffffff;
        background: #222222;
      }
    }
  }
}
