@import "../../styles/include-media";

.ReactModal__Overlay {
  background-color: inherit !important;

  &::after {
    content: '';
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(#fff, .3);
    filter: blur(10vh);
  }
}

.feedback-modal {
  max-width: 300px;
  width: 100%;
  min-height: 450px;
  height: 450px;
  max-height: calc(100vh - 30px);
  background-color: #000;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 25px 2px rgba(#fff, .5);
  border-radius: 4px;

  .h3 {
    font-size: 18px;
    margin: 0;
    padding: 0;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    user-select: none;
  }

  .close-btn {
    display: block;
    cursor: pointer;
    position: absolute;
    z-index: 4;
    right: 15px;
    top: 15px;
    color: #ffffff;
    transition: transform .3s ease;
    &:hover {
      @include media('>960px') {
        transform: rotate(180deg) scale(1.2);
      }
    }
  }
}

.form {
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: inherit;

  &-group { position: relative; }

  &.validated {
    input:invalid,
    textarea:invalid {
      border: 1px solid #e64943;
      + .invalid-message { opacity: 1; }
    }
  }

  input, textarea {
    max-width: 250px;
    width: 100%;
    appearance: none;
    outline: 0;
    border: 1px solid rgba(255, 255, 255, 0.4);
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 3px;
    padding: 5px 15px;
    margin: 0 auto 10px auto;
    display: block;
    font-size: 18px;
    color: white;
    transition-duration: 0.25s;
    font-weight: 300;
  }

  textarea {
    padding: 10px;
    height: 120px;
    resize: none;
  }

  .btn {
    margin: 0 auto;
    padding: 5px 10px;
    max-width: 100px;
    width: 100%;
    display: block;
    border: none;
    border-radius: 4px;
    background-color: #e0e0e0;
    transition: background-color .3s ease;
    text-transform: uppercase;
    font-weight: bold;
    color: #222222;
    cursor: pointer;
    outline: none;
    &:hover { background-color: #fff; }
  }

  .invalid-message {
    display: block;
    position: absolute;
    left: 10px;
    bottom: -15px;
    color: #b71c1c;
    font-size: 12px;
    opacity: 0;
    transition: all .3s ease;
  }

}

.success-message {
  position: absolute;
  z-index: 6;
  top: 0;
  left: 0;
  width: 100%;
  height: inherit;
  background: #fff;
  padding: 15px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  text-align: center;
  color: #222222;

  .h2 {
    font-size: 28px;
    line-height: 1.24em;
    font-weight: bold;
  }

  .h4 {
    margin: 30px 0;
    font-size: 20px;
    line-height: 1.24em;
    font-weight: bold;
  }

  .close-btn--success {
    cursor: pointer;
    display: block;
    font-size: 12px;
    text-transform: uppercase;
    width: 120px;
    height: 35px;
    line-height: 35px;
    background-color: #221122;
    border-radius: 4px;
    color: aliceblue;
    transition: all .3s ease;
    &:hover {
      @include media('>960px') { background-color: rgba(#221122, .8); }
    }
  }
}
