.animated-core {
  pointer-events: none;
  width: 120px;
  height: 120px;
  transform-style: preserve-3d;
  position: absolute;
  top: calc(50% - 60px);
  left: calc(50% - 60px);
  transform: translate(-50%, -50%) rotateX(60deg) rotateZ(-30deg);
  animation: rotate 20s infinite linear;

  .circle {
    width: 120px;
    height: 120px;
    position: absolute;
    transform-style: preserve-3d;
    border-radius: 100%;
    box-sizing: border-box;
    box-shadow: 0 0 60px rgba(crimson, 1), inset 0 0 60px rgba(crimson, 1);

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      width: 10%;
      height: 10%;
      border-radius: 100%;
      background: rgba(crimson, 1);
      box-sizing: border-box;
      box-shadow: 0 0 60px 2px rgba(crimson, 1);
    }
    &::before { transform: translateZ(-90px) }
    &::after { transform: translateZ(90px) }

    @for $i from 1 through 5 {
      &:nth-child(#{$i}) {
        transform: rotateZ($i / 5 * 360deg) rotateX(63.435deg)
      }
    }
  }
}

@keyframes rotate {
  0% {
    transform: rotateX(0) rotateY(0) rotateZ(0);
  }
  100% {
    transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
  }
}
