html {
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after { box-sizing: inherit; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
  color: #222;
  font: 400 16px/1.24em -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;;
  min-width: 320px;
  height: 100%;

}

#root {
  height: inherit;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a { text-decoration: none; }
